.body {
  background-color: rgb(3, 142, 159);
  border: 3px solid rgb(14, 100, 120);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 50% 30px;
  border-bottom-right-radius: 50% 30px;
  height: 565px;
  width: 325px;
  z-index: -1;
}

.power-box {
  /*   background-color: blue; */
  top: 78px;
  left: 9px;
  height: 22px;
  position: absolute;
}

.power-light {
  background-color: red;
  position: relative;
  left: 1px;
  height: 10px;
  width: 10px;
  border-radius: 50%;
}

.power-light:hover {
  background-color: red;
}

.power-status {
  color: darkgray;
  font-weight: bold;
  line-height: 0.5;
  position: absolute;
  right: 1px;
  top: 0px;
  transform: scaleY(0.5);
}

.power-text {
  color: darkgray;
  font-family: 'Gill Sans';
  font-size: xx-small;
}

.screen-box {
  background-color: rgb(54, 58, 61);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-left-radius: 50% 15px;
  border-bottom-right-radius: 50% 15px;
  box-shadow: 1px 1px black;
  height: 260px;
  width: 295px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  top: 17px;
}

.screen-display {
  background-color: lightgray;
  border: 5px solid black;
  border-radius: 3px;
  box-shadow: inset 0 0 10px gray;
  height: 165px;
  width: 180px;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  top: 25px;
  text-align: center;
}

.screen-logo {
  padding:3px;
  background-image: linear-gradient(
    to right,
    blue 19%,
    green,
    magenta 45%,
    red,
    yellow 75%
  );
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font: italic bold x-large 'futura', sans-serif;
  position: absolute;
  /*top: 10%;*/
  z-index: 1;
}

.screen-logo-shadow {
  font: italic bold x-large 'futura', sans-serif;
  position: absolute;
  /*top: 2px;*/
  transform: translateY(1px);
}

.nintendo-logo-screen {
  font-family: 'Gill Sans', sans-serif;
  font-weight: bold;
  font-size: small;
  position: relative;
  top:25px;
  /*margin-top: 30px;*/
}

.gameboy-color-logo {
  position: relative;
  bottom: -43px;
  height: 30px;
  margin-left: auto;
  margin-right: auto;
  transform: scaleY(1.2);
  width: 165px;
}

.logo-gb {
  color: darkgray;
  font: italic bold large 'futura', sans-serif;
  position: relative;
}

.logo-color {
  display: inline-block;
  font-family: 'Comic Sans MS';
  font-weight: bold;
  position: relative;
}

.logo-color-c {
  color: mediumvioletred;
  display: inline-block;
  transform: scaleY(1.2);
}

.logo-color-o1 {
  color: slateblue;
}

.logo-color-l {
  color: limegreen;
  display: inline-block;
  transform: rotate(-10deg) scaleY(1.1);
}

.logo-color-o2 {
  color: yellow;
}

.logo-color-r {
  color: teal;
  display: inline-block;
  transform: scaleY(1.2);
}

.nintendo-logo-body {
  border: 2px solid rgb(0, 107, 128);
  border-radius: 10px/50%;
  box-sizing: border-box;
  color: rgb(0, 107, 128);
  font-family: 'Gill Sans', sans-serif;
  font-weight: bold;
  margin-left: auto;
  margin-right: auto;
  padding: 2px;
  position: relative;
  text-align: center;
  top: 40px;
  height: 25px;
  width: 100px;
}

.button-box {
  /*   background-color: blue; */
  height: 85px;
  width: 282px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  top: 45px;
}

.arrow-group {
  height: 85px;
  width: 85px;
  /*   background-color: jaune; */
  position: absolute;
}

.up-box {
  background-color: rgb(127, 117, 116);
  border-radius: 4px 4px 0 0;
  box-shadow: 2px 2px 4px black, inset 2px 2px 4px darkgray, -1px -1px 2px black;
  height: 33%;
  width: 33%;
  left: 33%;
  position: absolute;
}

.right-box {
  background-color: rgb(127, 117, 116);
  border-radius: 0 4px 4px 0;
  box-shadow: 2px 2px 4px black, inset 0px 4px 4px -2px darkgray,
    -1px -1px 2px black;
  height: 33%;
  width: 33%;
  right: 0%;
  top: 33%;
  position: absolute;
}

.down-box {
  background-color: rgb(127, 117, 116);
  border-radius: 0 0 4px 4px;
  box-shadow: 2px 2px 4px black, inset 4px 0px 4px -2px darkgray,
    -1px -1px 2px black;
  height: 34%;
  width: 33%;
  bottom: 0%;
  left: 33%;
  position: absolute;
}

.center-box {
  background-color: rgb(127, 117, 116);
  height: 33%;
  width: 33%;
  left: 33%;
  top: 33%;
  position: absolute;
}

.left-box {
  background-color: rgb(127, 117, 116);
  border-radius: 4px 0 0 4px;
  box-shadow: 0px 4px 4px -2px black, inset 2px 2px 4px darkgray,
    -1px -1px 2px black;
  height: 33%;
  width: 33%;
  top: 33%;
  position: absolute;
}

.arrow {
  height: 0;
  width: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 18px solid rgb(75, 70, 66);
  left: 6px;
  top: 5px;
  position: absolute;
  z-index: 1;
}

.arrow::after {
  content: '';
  height: 0;
  width: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 15px solid rgb(89, 79, 77);
  left: -7px;
  top: 3px;
  position: absolute;
}

.right {
  transform: rotate(90deg);
}

.down {
  transform: rotate(180deg);
}

.left {
  transform: rotate(270deg);
}

.dent {
  background-color: rgb(75, 70, 66);
  border-radius: 50%;
  left: 6px;
  top: 6px;
  position: absolute;
  height: 17px;
  width: 17px;
}

.dent-highlight {
  background-color: rgb(155, 145, 144);
  border-radius: 50%;
  box-shadow: 0 0 6px 3px rgb(155, 145, 144);
  height: 5px;
  width: 5px;
  position: absolute;
  right: 2px;
  bottom: 2px;
}

.ab-button {
  background-color: rgb(78, 70, 68);
  border-radius: 50%;
  box-shadow: 2px 2px 2px black, inset 2px 2px 2px darkgray, -1px -1px 2px black;
  font-family: 'Gill Sans', sans-serif;
  color: rgb(55, 40, 47);
  text-shadow: 1px 1px 1px rgb(89, 79, 77);
  height: 40px;
  width: 40px;
  position: absolute;
}

.a {
  right: 0px;
  top: 10px;
}

.b {
  bottom: 13px;
  right: 62px;
}

.button-text-height {
  transform: scaleY(2);
  position: absolute;
  top: 11px;
  left: 14px;
}

.bottom-box {
  /*   background-color: blue; */
  height: 85px;
  width: 282px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  top: 76px;
  /* border: 5px solid green; */
}

.pill-button {
  background-color: rgb(81, 88, 80);
  border-radius: 8px/50%;
  box-shadow: 2px 2px 2px black, inset 2px 1px 2px darkgray, -1px -1px 2px black;
  color: rgb(0, 107, 128);
  font-family: 'Gill Sans', sans-serif;
  /*font-size: 10px;*/
 /* text-shadow: 1px 1px rgb(1, 127, 148), -1px -1px 0.5px rgb(76, 199, 215);*/
  height: 10px;
  width: 33px;
  position: absolute;
  font-size: 11px;
  text-shadow: none;
  font-weight:bold;
  /* bottom: 92px; */
}

.button-select {
  left: 100px;
  top: 52px;
}

.button-start {
  right: 100px;
  top: 52px;
}

.select {
  position: relative;
  top:20px;
  left:-5px;
}

.start {
  position: relative;
  top:20px;
}

.speaker {
  top: 0px;
  right: 0px;
  position: absolute;
  transform: skew(0, -10deg);
}

.row1 {
  position: relative;
  top: -8px;
  left: 10px;
}

.row2 {
  position: relative;
  top: -15px;
}

.row3 {
  position: relative;
  top: -20px;
  transform: rotate(180deg);
}

.row4 {
  position: relative;
  top: -32px;
}

.row5 {
  position: relative;
  top: -37px;
  transform: rotate(180deg);
}

.row6 {
  position: relative;
  top: -49px;
}

.row7 {
  position: relative;
  top: -53px;
  transform: rotate(180deg);
}

.row8 {
  position: relative;
  right: 10px;
  top: -60px;
  transform: rotate(180deg);
}

.dot-hole {
  background-color: black;
  border-radius: 50%;
  display: inline-block;
  height: 6px;
  width: 6px;
  margin-right: 10px;
  position: relative;
}

.dot-hole::after {
  content: '';
  background-color: rgb(3, 106, 121);
  border-radius: 50%;
  height: 6px;
  width: 6px;
  position: absolute;
  top: 0;
  left: 10px;
}

.row1 .dot-hole,
.row2 .dot-hole,
.row4 .dot-hole,
.row6 .dot-hole {
  box-shadow: 1px 1px 1px rgb(76, 199, 215);
}

.row3 .dot-hole,
.row5 .dot-hole,
.row7 .dot-hole,
.row8 .dot-hole {
  box-shadow: -1px -1px 1px rgb(76, 199, 215);
}

.row1 .dot-hole::before,
.row2 .dot-hole::before,
.row4 .dot-hole::before,
.row6 .dot-hole::before {
  content: '';
  background-color: rgb(76, 199, 215);
  border-radius: 50%;
  box-shadow: 0 0 1px 1px rgb(76, 199, 215);
  display: inline-block;
  height: 1px;
  width: 1px;
  left: 13px;
  bottom: 1px;
  position: absolute;
  z-index: 1;
}

.row3 .dot-hole::before,
.row5 .dot-hole::before,
.row7 .dot-hole::before,
.row8 .dot-hole::before {
  content: '';
  background-color: rgb(76, 199, 215);
  border-radius: 50%;
  box-shadow: 0 0 1px 1px rgb(76, 199, 215);
  display: inline-block;
  height: 1px;
  width: 1px;
  left: 12px;
  bottom: 3px;
  position: absolute;
  z-index: 1;
}


